/** utils **/

.hover {
  overflow: hidden;
  position: relative;
}

.hover-overlay {
    transition: all 0.4s;
  }

.hover img {
  transition: all 0.3s;
  width: 100%;
  height: 100%;
}

.hover-3::after {
    content: '';
    width: calc(100% - 3rem);
    height: calc(100% - 3rem);
    border: 1px solid #fff;
    position: absolute;
    top: 1.5rem;
    left: 1.5rem;
    z-index: 90;
    transition: all 0.3s;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    opacity: 0;
    display: block;
    opacity: 0;
  }

  .hover-3-content {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    text-align: center;
    z-index: 99;
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  .contact-info-content {
    display: flex;
    position: absolute;
    top: 45%;
    left: 0;
    width: 100%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    text-align: center;
    z-index: 99;
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    justify-content: space-between;
  }

  .hover-3-description {
    opacity: 0;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    transition: all 0.3s;
    text-decoration: none;
    color: white;
  }

  .hover-3-description:hover{
    color: rgb(187, 175, 175);
  }

  .instagram:hover{
    opacity: 0;
  }

  .hover-3 img {
    width: 110%;
    top: 50%;
    left: 50%;
  }

  .hover-3 .hover-overlay {
    background: rgba(0, 0, 0, 0.2);
  }

  .hover-3:hover img {
    width: 100%;
  }

  .hover-3:hover::after {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }

  .hover-3:hover .hover-3-description {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }

  .hover-3:hover .hover-overlay {
    background: rgba(0, 0, 0, 0.8);
  }

  .contact-info{
    text-align: left;
  }

  .insta{
    flex:1 1;
    justify-content: flex-end;
  }

  .leftContainer{
    display: flex;
    flex-direction: column;
  }

  .info{
    display: flex;
    flex-direction: column;
  }

  @media (max-width: 415px) {
    .hover-3-title{
      font-size: 18px;
    }

    .hover-3-description{
      font-size: 12px;
    }
    
  }
